.section-title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px; /* identical to box height, or 120% */

  @media (min-width: @tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}
